body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  background: #111;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.slidingPuzzle {
  display: grid;
  place-items: center;
}

h1 {
  margin: 0;
}

.board {
  position: relative;
  padding: 0;
}

.tile {
  position: absolute;
  list-style: none;
  background: #ec6f66;
  display: grid;
  place-items: center;
  font-size: 20px;
}

.button {
  display: block;
  padding: 20px;
  border-radius: 5px;
  background-color: #002df7;
  color: white;
  font-size: 20px;
}