h1 {
  text-align: center;
  margin-bottom: 1em;
}

.App {
  height: auto;
  width: auto;
  display: flex;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 1em;
}

.cardsBG {
  height: 60%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 1em;
}

.card {
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  transform: rotateY(180deg);
  animation: 2s hideCard linear;
  transition: transform 0.5s;
  width: 100px;
  height: 100px;
}

@keyframes hideCard {

  0%,
  70% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(180deg);
  }
}

.card-img {
  max-width: 80%;
  max-height: 80%;
  transition: transform 0.5s;
  transform: scale(0);
  animation: 2s hideImage linear;
}

@keyframes hideImage {

  0%,
  70% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

.card.active {
  transform: rotateY(0);
}

.card.correct {
  background-color: #65e469;
}

.card.wrong {
  background-color: #fd245a;
}

.card.active img {
  transform: scale(1);
}


.tile {
  width: 100px;
  height: 100px;
}